<template>
  <div class="cours-components-container">
    <div class="wrapper-latest-cours-add-container" id="wrapper-cours-latest-add">

    </div>
    <div class="cours-middle-container" id="cours-middle-container">
      <div class="cours-search-container">
        <input type="text" placeholder="Recherche">
        <button>Afficher tous</button>
      </div>
      <div class="button-cours-container" id="button-cours-container">
        <div class="cours-button-deployed" @click="deployed1A" id="cours-button-shpi1a" v-on:mouseenter="hover1a = true"
          v-on:mouseleave="hover1a = false">
          SHPI 1A
        </div>
        <div class="cours-button-deployed" @click="deployed2A" id="cours-button-shpi2a" v-on:mouseenter="hover2a = true"
          v-on:mouseleave="hover2a = false">
          SHPI 2A
        </div>
        <div class="cours-button-deployed" id="cours-button-shpi3a" v-on:mouseenter="hover3a = true"
          v-on:mouseleave="hover3a = false">
          Cycle Ingénieur 3A
        </div>
      </div>
    </div>

    <SHPI1AComponentVue></SHPI1AComponentVue>
    <SHPI2AComponentVue></SHPI2AComponentVue>

  </div>
</template>

<script>
import SHPI1AComponentVue from './1a/SHPI1AComponent.vue';
import SHPI2AComponentVue from './2a/SHPI2AComponent.vue';

export default {

  components: {
    SHPI1AComponentVue,
    SHPI2AComponentVue
  },
  data() {
    return {
      isExpanded1A: false,
      isExpanded2A: false,
      isExpanded3A: false,
      hover1a: false,
      hover2a: false,
      hover3a: false
    }
  },
  methods: {
    hideMainPage: function (bool) {
      if (bool) {
        document.getElementById('wrapper-cours-latest-add').style.visibility = "hidden";
        document.getElementById('wrapper-cours-latest-add').style.height = "5%";
        document.getElementById('cours-middle-container').style.height = "30%";
        document.getElementById('button-cours-container').style.alignItems = "flex-end";
        document.getElementById('cours-button-shpi1a').style.borderRadius = "15px 15px 0px 0px";
        document.getElementById('cours-button-shpi2a').style.borderRadius = "15px 15px 0px 0px";
        document.getElementById('cours-button-shpi3a').style.borderRadius = "15px 15px 0px 0px";
      } else {
        document.getElementById('wrapper-cours-latest-add').style.visibility = "visible";
        document.getElementById('wrapper-cours-latest-add').style.height = "50%";
        document.getElementById('cours-middle-container').style.height = "50%";
        document.getElementById('button-cours-container').style.alignItems = "center";
        document.getElementById('cours-button-shpi1a').style.borderRadius = "15px 15px";
        document.getElementById('cours-button-shpi2a').style.borderRadius = "15px 15px";
        document.getElementById('cours-button-shpi3a').style.borderRadius = "15px 15px";
      }
    },

    display1: function (bool) {
      if (bool) {
        document.getElementById('shpi1a-deployed').style.display = "flex";
        document.getElementById('cours-button-shpi1a').style.border = "5px solid var(--dark)";
        document.getElementById('cours-button-shpi1a').style.borderBottom = "none";
      } else {
        document.getElementById('shpi1a-deployed').style.display = "none";
        document.getElementById('cours-button-shpi1a').style.border = "none";
      }
    },

    display2: function (bool) {
      if (bool) {
        document.getElementById('shpi2a-deployed').style.display = "flex";
        document.getElementById('cours-button-shpi2a').style.border = "5px solid var(--dark)";
        document.getElementById('cours-button-shpi2a').style.borderBottom = "none";
      } else {
        document.getElementById('shpi2a-deployed').style.display = "none";
        document.getElementById('cours-button-shpi2a').style.border = "none";
      }
    },

    deployed1A: function () {
      if (!this.isExpanded2A && !this.isExpanded1A && !this.isExpanded3A) {
        this.hideMainPage(true);
        this.display1(true);
      }
      else if (this.isExpanded1A) {
        this.hideMainPage(false);
        this.display1(false);
      }
      else if (this.isExpanded2A) {
        this.display2(false);
        this.display1(true);
        this.isExpanded2A = !this.isExpanded2A;
      }
      else if (this.isExpanded3A) {
        console.log("oui");
      }
      this.isExpanded1A = !this.isExpanded1A;
    },

    deployed2A: function () {
      if (!this.isExpanded2A && !this.isExpanded1A && !this.isExpanded3A) {
        this.hideMainPage(true);
        this.display2(true);
      }
      else if (this.isExpanded2A) {
        this.hideMainPage(false);
        this.display2(false);
      }
      else if (this.isExpanded1A) {
        this.display1(false);
        this.display2(true);
        this.isExpanded1A = !this.isExpanded1A;
      }
      else if (this.isExpanded3A) {
        console.log('oui');
      }
      this.isExpanded2A = !this.isExpanded2A;
    }
  },
  watch: {
    hover1a(newValue) {
      if (!this.isExpanded1A && !this.isExpanded2A && !this.isExpanded3A) {
        if (newValue) {
          document.getElementById('cours-button-shpi1a').style.transform = "scale(1.2)";
        } else {
          document.getElementById('cours-button-shpi1a').style.transform = "scale(1)";
        }
      } else {
        document.getElementById('cours-button-shpi1a').style.transform = "scale(1)";
      }
    },
    hover2a(newValue) {
      if (!this.isExpanded1A && !this.isExpanded2A && !this.isExpanded3A) {
        if (newValue) {
          document.getElementById('cours-button-shpi2a').style.transform = "scale(1.2)";
        } else {
          document.getElementById('cours-button-shpi2a').style.transform = "scale(1)";
        }
      } else {
        document.getElementById('cours-button-shpi2a').style.transform = "scale(1)";
      }
    },
    hover3a(newValue) {
      if (!this.isExpanded1A && !this.isExpanded2A && !this.isExpanded3A) {
        if (newValue) {
          document.getElementById('cours-button-shpi3a').style.transform = "scale(1.2)";
        } else {
          document.getElementById('cours-button-shpi3a').style.transform = "scale(1)";
        }
      } else {
        document.getElementById('cours-button-shpi3a').style.transform = "scale(1)";
      }
    }
  }
}
</script>

<style lang="scss">
.cours-components-container {

  height: 100%;
  width: 100%;
  background-image: url('../../../../assets/1.svg');

  background-size: cover;
  background-repeat: no-repeat;

  .wrapper-latest-cours-add-container {
    width: 100%;
    height: 50%;
    transition: 0.5s;
  }

  .cours-middle-container {
    width: 100%;
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: 0.5s;

    .cours-search-container {
      width: 100%;
      height: 30%;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: space-between;

      input {
        width: 50%;
        height: 40%;
        font-size: var(--text-size-button-content);
        border-radius: 15px 15px;
        padding-left: 15px;
        outline: none;
        border: none;
        background-color: var(--color-background-card-container);
        box-shadow: 0 8px 12px 0 rgba(57, 17, 69, .2);
      }

      button {
        border: none;
        border-radius: 15px 15px;
        width: 20%;
        height: 40%;
        cursor: pointer;
        background-color: var(--color-background-card-container);
        box-shadow: 0 8px 12px 0 rgba(57, 17, 69, .2);
        font-size: var(--text-size-button-content);
      }
    }

    .button-cours-container {
      width: 90%;
      height: 70%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      transition: 0.5s;


      .cours-button-deployed {
        height: 60%;
        width: 25vw;
        border-radius: 15px 15px;
        cursor: pointer;
        background-color: var(--color-background-card-container);
        box-shadow: 0 8px 12px 0 rgba(57, 17, 69, .2);
        color: black;
        font-size: var(--text-size-button-content);
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        transition: 0.5s;
        // &:hover {
        //   transform: scale(1.2);
        // }
      }

    }

  }
}
</style>