<script>
import ActusSpecialityCardVue from '../card/ActusSpecialityCard.vue';
export default {
  components: {
    ActusSpecialityCardVue,
  },
  methods: {
    pageScroll: function () {
      const wrapperSpeciality = document.querySelector('.table-sepciality-container');
      const wrapperActuSpeciality = document.querySelector('.wrapper-actu-speciality-container');

      setInterval(() => {
        wrapperSpeciality.scroll(({
          top: wrapperSpeciality.scrollTop + 6,
          behavior: 'smooth'
        }))
        let maxScrollSpeciality = wrapperSpeciality.scrollHeight - wrapperSpeciality.clientHeight;
        if (wrapperSpeciality.scrollTop == maxScrollSpeciality) {
          wrapperSpeciality.scrollTop = 0;
        }
        wrapperActuSpeciality.scroll(({
          left: wrapperActuSpeciality.scrollLeft + 20,
          behavior: 'smooth'
        }))
        let maxScrollActuSpeciality = wrapperActuSpeciality.scrollWidth - wrapperActuSpeciality.clientWidth;
        if (wrapperActuSpeciality.scrollLeft == maxScrollActuSpeciality) {
          wrapperActuSpeciality.scrollLeft = 0;
        }
      }, 100)
    }
  },
  mounted() {
    this.pageScroll();
  }

}
</script>

