<template>
  <div class="forum-page-home-container">
    <div class="top-forum-container">
        <div class="wrapper-forum-container">
            <div class="wrapper-forum-card">
                <ForumCardVue v-for="(index) in 14" :key="index"></ForumCardVue>
            </div>
        </div>
    </div>
    <div class="bottom-forum-container">
        <div class="forum-buttons-container">
            <div class="first-button-container">
                <div class="forum-bottom-button-container">
                    Interview
                </div>
                <div class="forum-bottom-button-container">
                    Podcast
                </div>
            </div>
            <div class="second-button-container">
                <div class="forum-bottom-button-container">
                    Articles/Tutos
                </div>
                <div class="forum-bottom-button-container">
                    Tous
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import ForumCardVue from '../card/ForumCard.vue';

export default {
    components: {
        ForumCardVue,
    },
    methods: {
        pageScroll: function() {
            const wrapperForum = document.querySelector('.wrapper-forum-container');
            setInterval( () => {
                wrapperForum.scroll(({
                    left: wrapperForum.scrollLeft +20,
                    behavior: 'smooth'
                }))
                let maxScrollAuthor = wrapperForum.scrollWidth - wrapperForum.clientWidth;
                if (wrapperForum.scrollLeft == maxScrollAuthor) {
                    wrapperForum.scrollLeft = 0;
                }
            }, 100)
        }
    },
    mounted() {
        this.pageScroll();
    }
}
</script>

<style lang="scss">
.forum-page-home-container{
    height: 100%;
    width: 100%;
    .top-forum-container{
        width: 100%;
        height: 60%;
        display: flex;

        .wrapper-forum-container{

            width: 90%;
            height: 80%;
            background-color: var(--color-background-card-container);
            box-shadow: 0 8px 12px 0 rgba(57,17,69,.2);
            border-radius: 15px 15px;
            margin-left: 5%;
            margin-top: 3%;
            overflow-x: auto;
            overflow-y: hidden;

            .wrapper-forum-card {

                padding: 0;
                display: inline-flex;
                height: 100%;
                width: 100%;
                justify-content: space-between;
                align-items: center;

            }
        }

    }
    .bottom-forum-container{
        height: 40%;
        width: 100%;
        .forum-buttons-container {

            margin-left: 5%;
            display: flex;
            width: 90%;
            height: 100%;

            .first-button-container, .second-button-container {
                width: 50%;
                height: 100%;
                display: flex;
                .forum-bottom-button-container{
                    box-shadow: 0 8px 12px 0 rgba(57,17,69,.2);
                    width: 40%;
                    margin-left: 10%;
                    height: 30%;
                    border-radius: 15px 15px;
                    background-color: var(--color-background-card-container);
                    font-size: var(--text-size-button-content);
                    margin-top: 10%;
                    cursor: pointer;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: 0.5s;
                    &:hover{
                        transform: scale(1.2);
                    }
                }
                @media screen and (max-width: 1400px){
                    display: inline;
                    .forum-bottom-button-container{
                        margin-left: 30%;
                    }
                }
            }





        }

    }
}
</style>