<template>
    <div class="left-container">
        <div class="test-card">
            <h1 class="actus">Actus :</h1>
            <div class="wrapper">
                <ActusCardVue v-for="(index) in 2" :key="index"></ActusCardVue>
            </div>
        </div>
        <div class="text-welcome-page">
            <h1>Delmoo c'est: </h1>
            <div class="welcome-users">
                <h2>&nbsp;&nbsp;&nbsp;&nbsp;{{ users }}&nbsp;&nbsp;</h2>
                <h3> utilisateurs</h3>
            </div>
            <div class="welcome-cours">
                <h2>&nbsp;&nbsp;&nbsp;&nbsp;+{{ countCours }}&nbsp;&nbsp;</h2>
                <h3>Cours</h3>
            </div>
        </div>

    </div>
    <div class="right-container">
        <transition name="fade">
            <div class="right-image-main-logo" v-if="show"></div>
        </transition>
    </div>
</template>

<script>
import anime from 'animejs';
import ActusCardVue from '../card/ActusCard.vue';

export default {

    components: {
        ActusCardVue
    },

    data() {
        return {
            num: 200,
            users: 0,
            is_comma: true,
            cours: 50,
            countCours: 0,
            show: false
        }
    },
    watch: {
        num (val) {
            this.setCount(val)
        },
        cours (val) {
            this.setCount(val)
        }
    },
    methods: {
        setCount(val1, val2) {
            const objCount = { n: this.users, j: this.countCours}
            anime({
                targets: objCount,
                n: val1,
                j: val2,
                round: 1,
                duration: 1000,
                easing: 'linear',
                update: () => {
                    this.countCours = objCount.j;
                    this.users = objCount.n;
                }
            })
        }
    },
    mounted() {
        this.setCount(this.num, this.cours);
        this.show = true;
    }

}
</script>

<style lang="scss">
