    }

    @media screen and (max-width: 1400px) {
      visibility: hidden;
      width: 0;
    }
}

.left-container {
    width: 50vw;
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 1400px) {
        width: 100%;
