        box-shadow: 0 8px 12px 0 rgba(57,17,69,.2);
        border-radius: 15px 15px;

        .actus {
            font-size: var(--text-size-page-title);
            font-weight: bold;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 1vmin;
            margin-right: 0;
            margin-bottom: 0;
            margin-left: 0;
            height: 5vmin;
        }

        .wrapper {
            padding: 0;
            display: inline-flex;
            height: calc(100% - 6vmin);
            width: 100%;
            justify-content: space-between;
            align-items: center;
        }
    }

    .text-welcome-page {
        width: 50vw;
        height: 35vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
